#root {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
  height: 100vh;
  
  background-image: url(./img/background.png);
  background-size: cover;
  background-repeat: no-repeat;
  background-position: center;
}


button {
  border: thick outset cyan;
  border-radius: 1rem;
  padding: 0.5rem 1rem;
  background-color: rgb(218, 255, 255);
}

button:hover {
  border-style: solid;
}

button:active {
  border-style: inset;
}

button.active {
  border-color: rgb(47, 255, 196);
}


header {
  margin: 0;
  padding: 0 1rem 0 1rem;
  color: white;
  background-color: rgba(0, 128, 128, 0.5);
  border-radius: 3rem;
}


label {
  display: inline-block;
  text-align: right;
  min-width: 6rem;
}


main {
  max-height: 80vh;
  min-width: 60vw;
  overflow-y: auto;
}


.menu {
  list-style-type: none;
  padding: 0;
  display: flex;
}

.horizontal {
  flex-direction: row;
}

.vertical {
  flex-direction: column;
}

.menu > * {
  display: inline-block;
  border: thick solid gray;
  border-radius: 1rem;
  padding: 0.5rem 1rem;
  margin: 0.5rem;
  background-color: white;
  text-decoration: none;
}

.menu > *:hover {
  background-color: #eee;
  border-style: solid;
}

.menu > *:active {
  background-color: #999;
  border-style: inset;
}


.todo.completed {
  text-decoration: line-through;
}

.todo.not-mine {
  font-style: italic;
}

.todo.past-due {
  font-weight: bolder;
}


.viz.container {
  text-align: center;
  height: 100%;
  width: 100%;
}

.viz.timeline {
  width: 100%;
  height: 100%;
}

.viz.urgency {
  width: 100%;
  height: 100%;
}

.viz.popup {
  position: fixed;
  height: auto;
  width: auto;
  background-color: rgb(253, 234, 206);
  border: thick ridge rgb(255, 218, 163);
  border-radius: 1rem;
  padding: 0.5rem 1rem;
  display: none;
}